<script>
import Vue from 'vue'
import { IconsPlugin } from 'bootstrap-vue'
Vue.use(IconsPlugin)
export default {
  name: 'EcoFooter',
  data(){
      return {
        activeLanguage: 'en',
        translations:{
            en:{
                contact: "Contact Us",
                contactURL: "/about-us/leadership/contact-us/",
                memberships: "Memberships & Designations",
                beahr: "BEAHR Indigenous Training",
                employment: "Employment Programs",
                postSecondary: "Post-Secondary Program Accreditation",
                lifelong: "Lifelong Learning",
                lmr: "Labour Market Research",
                jobBoard:"Visit Our Job Board",
                aboutUs:"About Us",
                privacy: "Privacy Policy",


            },
            fr:{
                contact: "Contactez-nous",
                contactURL:"/a-propos/contactez-nous/",
                memberships: "Dsignations & adhsions",
                beahr: "Formation autochtone BEAHR",
                employment: "Programmes d'emploi",
                postSecondary: "Accrditation de programme postsecondaire",
                lifelong: "Apprentissage tout au long de la vie",
                lmr: "Recherche sur le march du travail",
                jobBoard:"Tableau des emplois",
                aboutUs:" propos",
                privacy: "Confidentialit"
            }
        }
      }
  },
  created:function(){
    var c = this.getCookie('pll_language')
    if(!c){
        c = 'en'
    }
    this.activeLanguage = c;
    console.log(this.activeLanguage)
  },
  methods:{
      getCookie:function(cname) {
      var name = cname + "=";
      var decodedCookie = decodeURIComponent(document.cookie);
      var ca = decodedCookie.split(';');
      for(var i = 0; i <ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') {
          c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
          return c.substring(name.length, c.length);
        }
      }
      return "";
    }
  },
  
  
}
</script>

